.box {
  position: absolute;
  top: var(--top);
  left: var(--left);
  width: var(--width);
  height: var(--height);
  border: solid 2px blue;
  background-color: transparent;
}

#fps-box {
  color: #fff;
  position: absolute;
  font-size: 14px;
  padding: 5px 10px;
  background: transparent;
}
